import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  ReactiveFormsModule,
  Validators,
} from '@angular/forms';
import { Router } from '@angular/router';
import { path } from '../../../models/routes.model';
import { InputFieldComponent } from '../../../shared/components/input-field/input-field.component';
import { CommonModule } from '@angular/common';
import { AuthService } from '../services/auth/auth.service';
import {
  BrowserStorageService,
  EStorageKeys,
} from '../../../helpers/browser-storage.service';
import { catchError, finalize, of, tap } from 'rxjs';

@Component({
  selector: 'app-login',
  standalone: true,
  imports: [ReactiveFormsModule, InputFieldComponent, CommonModule],
  templateUrl: './login.component.html',
  styleUrl: './login.component.scss',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoginComponent implements OnInit {
  public formGroup: FormGroup;
  private REGISTER = `/${path.LOGIN_REQUEST}`;
  private HOME = `/${path.HOME}`;
  private FORGOT_PASSWORD = `/${path.FORGOT_PASSWORD}`;

  public errorMessage = [{ username: '', password: '' }];
  isLoading: boolean = false;

  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private _authService: AuthService,
    private browserStorageService: BrowserStorageService
  ) {
    this.formGroup = this.formBuilder.group({
      username: ['', [Validators.required, Validators.email]],
      password: ['', [Validators.required, Validators.minLength(3)]],
    });
  }

  ngOnInit() {}

  onLogin() {
    if (!this.formGroup.valid) {
      this.formGroup.markAllAsTouched();
      // this.errorMessage = 'This is required.';
    } else {
      this.isLoading = true;
      console.log('isloading ', this.isLoading);

      const loginData = this.formGroup.value;
      this._authService
        .login(loginData.username, loginData.password)
        .pipe(
          tap((res) => {
            if (res) {
              this.isLoading = false;
              console.log('in isloading ', this.isLoading);

              this.browserStorageService.addToLocalStorage(
                EStorageKeys.USER,
                JSON.stringify(res.data.user)
              );
              this.router.navigate([this.HOME]);
            }
          }),
          catchError((err) => {
            this.isLoading = false;
            console.log('catch isloading ', err, this.isLoading);

            return of(err);
          }),
          finalize(() => {
            this.isLoading = false;
            console.log('final Iisloading ', this.isLoading);
          })
        )
        .subscribe();
    }
  }

  onForgotPassword() {
    this.router.navigate([this.FORGOT_PASSWORD]);
  }

  onSignUp() {
    this.router.navigate([this.REGISTER]);
  }
}
